import React, { FunctionComponent } from 'react'; // importing FunctionComponent
import { Link } from 'react-router-dom';

export default () => { 
  return(
    <aside>
      <h2>HOME</h2>
      <p>
      <div>
         <nav>
           <ul>
             <li>
               <Link to="/">Home</Link>
             </li>
             <li>
               <Link to="/terminal/A">Terminal A</Link>
             </li>
             <li>
               <Link to="/terminal/B">Terminal B</Link>
             </li>
           </ul>
         </nav>
         </div>
      </p>
    </aside>
  )
}