import React, { FunctionComponent } from 'react'; // importing FunctionComponent

type TerminalProps = {
  type: string
}

export const Terminal: FunctionComponent<TerminalProps> = ({ type }) => { 
  return(
    <aside>
      <h2>TERMINAL TYPE { type }</h2>
      <p>
        { "TEST" }
      </p>
    </aside>
  )
}